.confirmationDetails {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    margin-bottom: 10px;
    color: #730d73;
    font-family:'Times New Roman' ;

}
.confirmationDetails li {
    margin-bottom: 10px;
    color: #444;
}

.confirmationView {
    max-width: 800px;
    margin: auto;
    padding: 20px;
    background-color: #ffffff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    font-family:'Times New Roman' ;

}
.confirmationDetails ul {
    list-style: none;
    padding: 0;
}

.confirmationFooter {
    text-align: center;
    margin-top: 20px;
    color: #d5b5c9;
}

.confirmationView > div {
